<template>
    <!--  -->
    <SettingsBase>
        <h2 class="settings__heading">
            <router-link v-ripple class="settings__back-button" to="/settings/">
                <Icon icon="fluent:arrow-left-12-filled" width="25px" />
            </router-link>
            <Icon icon="fa-solid:sliders-h" width="19px" />
            <span class="ml-3"></span>
        </h2>
        <div class="settings__content">
            <div class="settings__item settings__item--sync-disabled">
                <div class="settings__item-heading"></div>
                <div class="settings__item-label">
                    <br>
                    <br>
                </div>
                <div class="settings__item-label">
                    [1080p (60fps)]  30fps (60i) <br>
                    [1080p (60fps)]  [] FFmpeg () <br>
                </div>
                <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense"
                    :items="tv_streaming_quality" v-model="settingsStore.settings.tv_streaming_quality">
                </v-select>
            </div>
            <div class="settings__item settings__item--switch settings__item--sync-disabled"
                :class="{'settings__item--disabled': PlayerUtils.isHEVCVideoSupported() === false}">
                <label class="settings__item-heading" for="tv_data_saver_mode"></label>
                <label class="settings__item-label" for="tv_data_saver_mode">
                    H.265 / HEVC  1/2 <br>
                     [] FFmpeg () <br>
                    <p class="mt-1 mb-0 error--text lighten-1" v-if="PlayerUtils.isHEVCVideoSupported() === false && Utils.isFirefox() === false">
                        
                    </p>
                    <p class="mt-1 mb-0 error--text lighten-1" v-if="PlayerUtils.isHEVCVideoSupported() === false && Utils.isFirefox() === true">
                         Firefox 
                    </p>
                </label>
                <v-switch class="settings__item-switch" id="tv_data_saver_mode" inset hide-details
                    v-model="settingsStore.settings.tv_data_saver_mode" :disabled="PlayerUtils.isHEVCVideoSupported() === false">
                </v-switch>
            </div>
            <div class="settings__item settings__item--switch settings__item--sync-disabled">
                <label class="settings__item-heading" for="tv_low_latency_mode"></label>
                <label class="settings__item-label" for="tv_low_latency_mode">
                    <b> 0.9 </b><br>
                     3  (1.1x) <br>
                    <br>
                </label>
                <v-switch class="settings__item-switch" id="tv_low_latency_mode" inset hide-details
                    v-model="settingsStore.settings.tv_low_latency_mode">
                </v-switch>
            </div>
            <v-divider class="mt-6"></v-divider>
            <div class="settings__item">
                <div class="settings__item-heading"></div>
                <div class="settings__item-label">
                    <br>
                </div>
                <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense"
                    :items="panel_display_state" v-model="settingsStore.settings.panel_display_state">
                </v-select>
            </div>
            <div class="settings__item">
                <div class="settings__item-heading"></div>
                <div class="settings__item-label">
                    <br>
                </div>
                <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense"
                    :items="tv_panel_active_tab" v-model="settingsStore.settings.tv_panel_active_tab">
                </v-select>
            </div>
            <v-divider class="mt-6"></v-divider>
            <div class="settings__item settings__item--switch">
                <label class="settings__item-heading" for="tv_show_superimpose"> {{Utils.AltOrOption()}} + /</label>
                <label class="settings__item-label" for="tv_show_superimpose">
                    112{{Utils.AltOrOption()}} <br>
                    <br>
                </label>
                <v-switch class="settings__item-switch" id="tv_show_superimpose" inset hide-details
                    v-model="settingsStore.settings.tv_channel_selection_requires_alt_key">
                </v-switch>
            </div>
            <v-divider class="mt-6"></v-divider>
            <div class="settings__item">
                <div class="settings__item-heading"></div>
                <div class="settings__item-label">
                    <br>
                     (KonomiTV-Settings.json) []  KonomiTV <br>
                </div>
            </div>
            <v-btn class="settings__save-button mt-4" depressed @click="exportSettings()">
                <Icon icon="fa6-solid:download" class="mr-3" height="19px" />
            </v-btn>
            <div class="settings__item">
                <div class="settings__item-heading error--text text--lighten-1"></div>
                <div class="settings__item-label">
                    [] <br>
                    <b></b><br>
                    <b></b><br>
                </div>
                <v-file-input class="settings__item-form" outlined hide-details placeholder=" (KonomiTV-Settings.json) "
                    :dense="is_form_dense"
                    accept="application/json"
                    prepend-icon=""
                    prepend-inner-icon="mdi-paperclip"
                    v-model="import_settings_file">
                </v-file-input>
            </div>
            <v-btn class="settings__save-button error mt-5" depressed @click="importSettings()">
                <Icon icon="fa6-solid:upload" class="mr-3" height="19px" />
            </v-btn>
            <div class="settings__item">
                <div class="settings__item-heading error--text text--lighten-1"></div>
                <div class="settings__item-label">
                    <br>
                    <br>
                    <b></b><br>
                </div>
            </div>
            <v-btn class="settings__save-button error mt-5" depressed @click="resetSettings()">
                <Icon icon="material-symbols:device-reset-rounded" class="mr-2" height="23px" />
            </v-btn>
        </div>
    </SettingsBase>
</template>
<script lang="ts">

import { mapStores } from 'pinia';
import Vue from 'vue';

import useSettingsStore from '@/stores/SettingsStore';
import Utils, { PlayerUtils } from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

const QUALITY_H264 = [
    {text: '1080p (60fps) (4.50GB/h / 10.0Mbps)', value: '1080p-60fps'},
    {text: '1080p (4.50GB/h / 10.0Mbps)', value: '1080p'},
    {text: '810p (2.62GB/h / 5.8Mbps)', value: '810p'},
    {text: '720p (2.18GB/h / 4.9Mbps)', value: '720p'},
    {text: '540p (1.52GB/h / 3.4Mbps)', value: '540p'},
    {text: '480p (1.06GB/h / 2.3Mbps)', value: '480p'},
    {text: '360p (0.60GB/h / 1.3Mbps)', value: '360p'},
    {text: '240p (0.35GB/h / 0.8Mbps)', value: '240p'},
];

const QUALITY_H265 = [
    {text: '1080p (60fps) (1.80GB/h / 4.0Mbps)', value: '1080p-60fps'},
    {text: '1080p (1.37GB/h / 3.0Mbps)', value: '1080p'},
    {text: '810p (1.05GB/h / 2.3Mbps)', value: '810p'},
    {text: '720p (0.82GB/h / 1.8Mbps)', value: '720p'},
    {text: '540p (0.53GB/h / 1.2Mbps)', value: '540p'},
    {text: '480p (0.46GB/h / 1.0Mbps)', value: '480p'},
    {text: '360p (0.30GB/h / 0.7Mbps)', value: '360p'},
    {text: '240p (0.20GB/h / 0.4Mbps)', value: '240p'},
];

export default Vue.extend({
    name: 'Settings-General',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            Utils: Utils,
            PlayerUtils: PlayerUtils,

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            tv_streaming_quality: QUALITY_H264,

            // 
            panel_display_state: [
                {text: '', value: 'RestorePreviousState'},
                {text: '', value: 'AlwaysDisplay'},
                {text: '', value: 'AlwaysFold'},
            ],

            // 
            tv_panel_active_tab: [
                {text: '', value: 'Program'},
                {text: '', value: 'Channel'},
                {text: '', value: 'Comment'},
                {text: 'Twitter ', value: 'Twitter'},
            ],

            //  (KonomiTV-Settings.json) 
            import_settings_file: null as File | null,
        };
    },
    computed: {
        // SettingsStore  this.settingsStore 
        // ref: https://pinia.vuejs.org/cookbook/options-api.html
        ...mapStores(useSettingsStore),
    },
    created() {
        if (this.settingsStore.settings.tv_data_saver_mode === true) {
            this.tv_streaming_quality = QUALITY_H265;
        }
    },
    watch: {
        'settingsStore.settings.tv_data_saver_mode': {
            immediate: true,
            handler(val: boolean) {
                if (val === true) {
                    this.tv_streaming_quality = QUALITY_H265;
                } else {
                    this.tv_streaming_quality = QUALITY_H264;
                }
            },
        }
    },
    methods: {

        // 
        exportSettings() {

            //  JSON 
            const settings_json = JSON.stringify(this.settingsStore.settings, null, 4);

            //  Blob KonomiTV-Settings.json 
            const settings_json_blob = new Blob([settings_json], {type: 'application/json'});
            Utils.downloadBlobData(settings_json_blob, 'KonomiTV-Settings.json');
            this.$message.success('');
        },

        // 
        async importSettings() {

            // 
            if (this.import_settings_file === null) {
                this.$message.error('');
                return;
            }

            // 
            const result = await this.settingsStore.importClientSettings(this.import_settings_file);
            if (result === true) {
                this.$message.success('');
                window.setTimeout(() => this.$router.go(0), 300);
            } else {
                this.$message.error('');
            }
        },

        // 
        async resetSettings() {
            await this.settingsStore.resetClientSettings();
            this.$message.success('');
            window.setTimeout(() => this.$router.go(0), 300);
        },
    }
});

</script>