                left: 50%;
                transform: translate(-50%, -50%);
                color: var(--v-background-lighten3);
                filter: drop-shadow(0px 0px 3px rgba(0, 0, 0, 0.3));
                opacity: 0;
                visibility: hidden;
                transition: opacity 0.2s cubic-bezier(0.4, 0.38, 0.49, 0.94), visibility 0.2s cubic-bezier(0.4, 0.38, 0.49, 0.94);
                will-change: opacity;
                z-index: 3;

                &--display {
                    opacity: 1;
                    visibility: visible;
                }
            }

            .watch-player__dplayer {
                width: 100%;
            }

            .watch-player__button {
                display: flex;
                justify-content: space-around;
                flex-direction: column;
                position: absolute;
                top: 50%;
                right: 28px;
                height: 190px;
                transform: translateY(-50%);
                opacity: 0;
                visibility: hidden;
                transition: opacity 0.3s, visibility 0.3s;
                @include tablet-vertical {
                    right: 15px;
                    height: 128px;
                }
                @include smartphone-horizontal {
                    right: 15px;
                    height: 155px;
                }
                @include smartphone-vertical {
                    right: 15px;
                    height: 100px;
                }

                .switch-button {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 48px;
                    height: 48px;
                    color: var(--v-text-base);
                    background: #2F221FC0;
                    border-radius: 7px;
                    transition: background-color 0.15s;
                    user-select: none;
                    cursor: pointer;
                    @include smartphone-horizontal {
                        width: 38px;
                        height: 38px;
                        border-radius: 5px;
                    }
                    @include smartphone-vertical {
                        width: 38px;
                        height: 38px;
                        border-radius: 5px;
                    }

                    &:hover {
                        background: #2F221FF0;
                    }
                    //  hover 
                    @media (hover: none) {
                        &:hover {
                            background: #2F221FC0;
                        }
                    }

                    svg {
                        @include smartphone-horizontal {
                            height: 27px;
                        }
                        @include smartphone-vertical {
                            height: 27px;
                        }
                    }

                    .switch-button-icon {
                        position: relative;
                    }

                    &-up > .switch-button-icon {
                        top: 6px;
                    }
                    &-panel {
                        @include tablet-vertical {
                            display: none;
                        }
                        @include smartphone-vertical {
                            display: none;
                        }
                    }
                    &-panel > .switch-button-icon {
                        top: 1.5px;
                        transition: color 0.4s cubic-bezier(0.26, 0.68, 0.55, 0.99);
                    }
                    &-down > .switch-button-icon {
                        bottom: 4px;
                    }
                }
            }
        }
    }

    .watch-panel {
        display: flex;
        flex-direction: column;
        flex-shrink: 0;
        width: 352px;
        height: 100%;
        background: var(--v-background-base);
        @include tablet-vertical {
            width: 100%;
            height: auto;
            flex-grow: 1;
        }
        @include smartphone-horizontal {
            width: 310px;
        }
        @include smartphone-vertical {
            width: 100%;
            height: auto;
            flex-grow: 1;
        }

        // content-visibility: hidden 
        @media (hover: none) {
            content-visibility: hidden;
        }

        .watch-panel__header {
            display: flex;
            align-items: center;
            flex-shrink: 0;
            width: 100%;
            height: 70px;
            padding-left: 16px;
            padding-right: 16px;
            @include tablet-vertical {
                display: none;
            }
            @include smartphone-horizontal {
                display: none;
            }
            @include smartphone-vertical {
                display: none;
            }

            .panel-close-button {
                display: flex;
                position: relative;
                align-items: center;
                flex-shrink: 0;
                left: -4px;
                height: 35px;
                padding: 0 4px;
                border-radius: 5px;
                font-size: 16px;
                user-select: none;
                cursor: pointer;

                &__icon {
                    position: relative;
                    left: -4px;
                }
                &__text {
                    font-weight: bold;
                }
            }

            .panel-broadcaster {
                display: flex;
                align-items: center;
                min-width: 0;
                margin-left: 16px;

                &__icon {
                    display: inline-block;
                    flex-shrink: 0;
                    width: 43px;
                    height: 24px;
                    border-radius: 3px;
                    background: linear-gradient(150deg, var(--v-gray-base), var(--v-background-lighten2));
                    object-fit: cover;
                    user-select: none;
                }

                &__number {
                    flex-shrink: 0;
                    margin-left: 8px;
                    font-size: 16px;
                }

                &__name {
                    margin-left: 5px;
                    font-size: 16px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    @include smartphone-horizontal {
                        font-size: 14px;
                    }
                }
            }
        }

        .watch-panel__content-container {
            position: relative;
            height: 100%;

            .watch-panel__content {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: var(--v-background-base);
                transition: opacity 0.2s, visibility 0.2s;
                opacity: 0;
                visibility: hidden;

                //  () 
                // 
                @media (hover: none) {
                    transition: none;
                    content-visibility: hidden;
                }
                &--active {
                    opacity: 1;
                    visibility: visible;
                    content-visibility: auto;
                }
            }

            .watch-panel__content-remocon-button {
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                right: 16px;
                bottom: 16px;
                width: 48px;
                height: 48px;
                border-radius: 50%;
                background: var(--v-background-lighten1);
                outline: none;
                transition: opacity 0.2s, visibility 0.2s;
                opacity: 0;
                visibility: hidden;

                @media (hover: none) {
                    transition: none;
                }
                &--active {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }

        .watch-panel__navigation {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            flex-shrink: 0;
            height: 77px;
            background: var(--v-background-lighten1);
            @include tablet-vertical {
                height: 48px;
                background: var(--v-background-base);
            }
            @include smartphone-horizontal {
                height: 34px;
            }
            @include smartphone-vertical {
                height: 44px;
                background: var(--v-background-base);
            }

            .panel-navigation-button {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                width: 77px;
                height: 56px;
                padding: 6px 0px;
                border-radius: 5px;
                color: var(--v-text-base);
                box-sizing: content-box;
                transition: color 0.3s;
                user-select: none;
                cursor: pointer;
                @include tablet-vertical {
                    width: 100px;
                    height: 40px;
                    padding: 5px 0px;
                    box-sizing: border-box;
                }
                @include smartphone-horizontal {
                    height: 34px;
                    padding: 5px 0px;
                    box-sizing: border-box;
                }
                @include smartphone-vertical {
                    height: 34px;
                    padding: 5px 0px;
                    box-sizing: border-box;
                }

                &--active {
                    color: var(--v-primary-base);
                    .panel-navigation-button__icon {
                        color: var(--v-primary-base);
                    }
                    @include tablet-vertical {
                        background: #5b2d3c;
                    }
                    @include smartphone-vertical {
                        background: #5b2d3c;
                    }
                }

                &__icon {
                    height: 34px;
                    @include tablet-vertical {
                        color: var(--v-text-base);
                    }
                    @include smartphone-vertical {
                        color: var(--v-text-base);
                    }
                }
                &__text {
                    margin-top: 5px;
                    font-size: 13px;
                    @include tablet-vertical {
                        display: none;
                    }
                    @include smartphone-horizontal {
                        display: none;
                    }
                    @include smartphone-vertical {
                        display: none;
                    }
                }
            }
        }
    }
}

</style>